.add-note {
    display: block;
    height: 28px;
    background: #f7f8f8;
    border: none;
    width: 100%;
    max-width: 400px;
    cursor: pointer;
    outline: none;
    z-index: 0;
    margin: 0 auto;
    border: 1px dashed gray;
    opacity: 0;
    transition: opacity .3s;
}

.add-note:hover {
    opacity: 1;
    transition: opacity .3s;
    background-color: rgb(208, 226, 194);
}